<template>
  <div ref="trys" style="height: calc(100%)">
    <div v-if="showInfo" class="info-container">
      <info :row="row" @closeDiv="closeDiv" type="0" :deviceTypes="searchParam.deviceType"></info>
    </div>
    <div v-show="!showInfo" class="all-container ">
      <div class="operate-div" ref="operateDiv" style="margin-right:0em;">
        <selects ref="selects" :option="checkSelOption" @comfim="comfimFunc" @getSelect="getCollectItem"></selects>
        <template v-for="(item, $index) in FormOpt">
          <selects ref="selects" v-if="item.type == 'select'" :option="item.options" @getSelect="getOption"></selects>
          <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="margin-right: 0;"></inputs>
          <datePicker ref="timer"  v-if="item.type == 'timer'" type='doubleDate'
                      :dateOpt="timeOpt" @getDatePicker="getOption"></datePicker>
        </template>
      <!--   <button class="button-operate" @click="deepSearch"><i class="hasIcon hasImg" >
          <img src="dist/img/measure/search-icon.png" />
        </i>高级搜索
        </button> -->
      </div>
      <!-- <div class="deepFilter" ref="deepFilter">
        <label>筛选条件：</label>
        <template v-for="(data, $index) in deepSearchData" v-if="data.label !== ''">
          <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}}： {{data.value}}
            <i v-if="data.flag"  @click=" removeSearchData($index)">
             <icon name="remove" scale="0.8"></icon></i></span>
        </template>
      </div> -->
      <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
        <tables ref="myTable" v-if="show" :tableOpt="TableOpt" class="tablePadding"
                @rowDbClick="clickTableRow"></tables>
      </div>
    </div>
    <dialogs type="customer" ref="deepSearch" width="45em" v-if="dataReady">
      <div slot="header">高级搜索</div>
      <div slot="content">
        <ul class="dialog-ul">
          <li v-for="(item, $index) in deepSearchList" class="formList" :class="item.classes">
            <label class="label-show">{{item.label}}</label>
            <template v-if="item.type === 'input'">
              <inputs :ref="item.options.key" :option="item.options" @getInput="getFrom"></inputs>
            </template>
            <template v-if="item.type === 'select'">
              <selects :ref="item.options.key" :option="item.options" @getSelect="getFrom"></selects>
            </template>
            <template v-if="item.type === 'timer'">
              <datePicker ref="timer" :dateOpt="item.options" @getDatePicker="getFrom"></datePicker>
            </template>
          </li>
        </ul>
      </div>
      <div slot="footer">
        <button class="button-default" @click="deepData">提交</button>
        <button class="button-default cancel-button" @click="cancelSearch">取消</button>
      </div>
    </dialogs>
  </div>
</template>
<script>
    import info from '../../childComponent/Infos.vue'
    export default {
        data() {
            return {
                dataReady: false,
                showInfo: false, //展示详情页面
                show: false, //延迟加载table组件
                controls: [],// button的控制权限
                searchParam: {
                  deviceType: '',
                  startDate: '',
                  endDate: '',
                  isRealTime: '',
                  types: ''
                },
                deepSearchParam: {  //高级查询
                  startDate: '',
                  endDate: '',
                  customerAddr: '',
                  propertyName: '',
                  customerName: '',
                  customerContact: '',
                  meterUserNo: '',
                  deviceNo: '',
                  status: '', //开户状态
                  isRealTime: ''//数据类型
                },
                measureDate: '',//时间段key
                tableHeight: '',
                deepSearchData: [],
                searchType: 'customerName',
                TableOpt: {
                        column: [],
                        rowName: 'data',
                        queryParam: {deviceType: '07'},
                        url: 'historyData/queryList',
                        method: 'post',
//                        url: 'dist/json/measureList.json',
//                        method: 'get',
                        pageSize: 20,
                        pageNumber: 1,
                        pageList: [20,40,60],
                        height: document.body.clientHeight - 410,
                        exportURL: 'historyData/generateExcel',
                       // isExport: true,
                        isSetExport: true
                    },
                //#region 选择采集框
              collectItem:["0001ff00"],//选择的采集项
              //#endregion
            }
        },
        components: {
            info
        },
        methods: {
          closeDiv: function () {
            this.showInfo = false;
            this.$refs.myTable.onResize();
            // this.$parent.showInfoDetail = true
          },
          deepSearch: function () {
            this.dataReady = true;
            let that = this;
            this.$nextTick(function() {
              that.$refs.deepSearch.show();
            })

          },
          //关闭并清空所有查询条件
          cancelSearch: function () {
            this.$refs.deepSearch.close();
            this.dataReady = false;
            this.deepSearchParam = this.cleanSearchData(this.deepSearchParam);//清空数据
          },
          // 调用公共方法获取自定义弹出框表单的数据
          getFrom: function (val) {
            if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startDate', 'endDate', 'isRealTime']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
          },
          //高级搜索确定查询
          deepData: function () {
            this.$refs['inputs'][0].search = '';
            let arr = this.getSearchData(this.deepSearchParam);
            this.searchParam = this.extendObj(this.cleanSearchData(this.searchParam, ['deviceType', 'startDate', 'endDate', 'isRealTime']), arr);
            this.getParams(1);
            this.cancelSearch();
          },
          getParams: function (pageNumber) {
            this.deepSearchData = [];
            let arr = this.getSearchData(this.searchParam);
            arr.types = this.collectItem;
            for (let key in arr) {
              let labelName = '';
              let value = arr[key];
              let removeFlag = true;
              switch (key) {
                case 'isRealTime':
                  labelName = '数据类型';
                  value = this.getEnumItem('isRealTime', value).text;
                  break;
                case 'status':
                  labelName = '开户状态';
                  value = this.getEnumItem('isAccount', value).text;
                  break;
                case 'customerName':
                  labelName = '用户姓名';
                  break;
                case 'customerAddr':
                  labelName = '用户地址';
                  break;
                case 'propertyName':
                  labelName = '门牌编号';
                  break;
                case 'customerContact':
                  labelName = '联系电话';
                  break;
                case 'meterUserNo':
                  labelName = '表计户号';
                  break;
                case 'startDate':
                  removeFlag = false;
                  labelName = '开始时间';
                  break;
                case 'endDate':
                  removeFlag = false;
                  labelName = '结束时间';
                  break;
                case 'deviceNo':
                  labelName = '用户表号';
                  break;
                case 'deviceType':
                  removeFlag = false;
                  labelName = '设备类型';
                  value = this.getEnumItem('deviceType', value).text;
                  break;
              }
              this.deepSearchData.push({label: labelName, value: value, key: key, flag: removeFlag});
            }
            this.getHeight();
            //根据查询条件调整表头
            if( this.$refs.myTable) {
                this.$refs.myTable.resetTableOpt({column:this.column,isLoadData:false});
                this.searchTable(arr, pageNumber);
            }
          },

          removeSearchData: function (index) {
            let key = this.deepSearchData[index].key;
              this.searchParam[key] = this.$refs['inputs'][0].search = '';
              this.getParams(1);
          },

          clickTableRow: function (val) {
            this.row = val.val;
            this.showInfo = true;
          //  this.$parent.showInfoDetail = false
          },
          resetDeepData: function(val) {
            let that = this;
            let data = that.initDate();
            that.searchParam.startDate = data.start;
            that.searchParam.endDate = data.end;
            that.deepSearchData = [];
            that.deepSearchData.push({label: '设备类型', value: this.getEnumItem('deviceType', val).text, key: 'deviceType', sign: false});
            that.deepSearchData.push({label: '开始时间', value: data.start, key: 'startDate', sign: false});
            that.deepSearchData.push({label: '结束时间', value: data.end, key: 'endDate', sign: false});
          },
          windowChange: function () {
  
              let that = this;
              that.getHeight();
            // window.onresize = function () {
            //   that.getHeight();
            // }
          },
          init: function () {
            let that = this;
            let data = that.initDate();
            that.TableOpt.isSetExport = that.controls[6];
            that.TableOpt.queryParam['startDate'] = data.start;
            that.TableOpt.queryParam['endDate'] = data.end;
            that.TableOpt.queryParam['types'] = that.collectItem;
            that.TableOpt.column = this.column;
            that.show = true;
          },
          //获取采集指令
          getCollectItem:function (resVal) {
            this.collectItem=resVal.val;
          },
          comfimFunc:function(){
            this.getParams(1);
          },
        },
        computed: {
          checkSelOption:function () {
            let arr = [{value:"0001ff00", text: '正向有功总',checked:true},
                  {value: "00900200", text: '账户余额',checked:false}]
                  // {value: "05060101", text: '日冻结',checked:false},
                 // {value: "00010001", text: '月冻结',checked:false}];
            if(this.searchParam.deviceType != '07') {
               arr = [{value:"0001ff00", text: '有功总示数',checked:true}]
            }
            return {
                type:"checkSelect",
                cs: 'client',
                width: '14em',
                data: arr,
                key: 'collectItem'
              }
          },
          FormOpt() {
            return [
              {
                type: 'select',
                options: {
                  cs: 'client',
                  width: '7.5em',
                  data: this.getEnumItem('deviceType').obj,
                  text: 'text',
                  value: 'value',
                  key: 'searchParam.deviceType'
                }
              },
              {
                type: 'timer'
              },
              {

                type: 'select',
                options: {
                   width: '7.5em',
                   cs: 'client',
                   data: this.getEnumItem('isRealTime').obj,
                    key: 'searchParam.isRealTime'
                }
              },
              {
                type: 'input',
                options: {
                  type: 'typeSearch',
                  key: 'searchParam',
                  selectOpt: {
                    width: '7em',
                    cs: 'client',
                    data: [
                      {text: '用户姓名', value: 'customerName'},
                      {text: '用户地址', value: 'customerAddr'},
                      {text: '门牌编号', value: 'propertyName'},
                      {text: '联系电话', value: 'customerContact'},
                      {text: '表计户号', value: 'meterUserNo'},
                      {text: '用户表号', value: 'deviceNo'}
                    ],
                    text: 'text',
                    value: 'value',
                    key: 'searchType'
                  }
                }
              }
            ]
          },
          deepSearchList() {
            let width = 'calc(100% - 6em)';
            if (this.searchParam.startDate !== '' && this.searchParam.endDate !== '') {
              return [
                {
                  classes: 'halfWidth',
                  label: '起始时间',
                  type: 'timer',
                  options: {
                    key: 'deepSearchParam.startDate',
                    value: this.searchParam.startDate,
                    width: width
                  }
                },
                {
                  classes: 'halfWidth hasMarginLi',
                  label: '结束时间',
                  type: 'timer',
                  options: {
                    key: 'deepSearchParam.endDate',
                    value: this.searchParam.endDate,
                    width: width
                  }
                },
                {
                  classes: 'halfWidth',
                  type: 'input',
                  isEmpty: true,
                  label: '用户姓名',
                  options: {
                    width: width,
                    type: 'normalInput',
                    placeHolder: '可输入中文、数字',
                    key: 'deepSearchParam.customerName',
                    length: 15,
                    value: '',
                    regular: 'name'
                  }
                },
                {
                  classes: 'halfWidth hasMarginLi',
                  type: 'input',
                  isEmpty: true,
                  label: '门牌编号',
                  options: {
                    width: width,
                    type: 'normalInput',
                    value: '',
                    placeHolder: '可输入字母数字、下划线、-',
                    key: 'deepSearchParam.propertyName',
                    length: 30,
                    limit: 'NCO'
                  }
                },
                {
                  classes: 'halfWidth',
                  type: 'input',
                  isEmpty: true,
                  label: '联系电话',
                  options: {
                    width: width,
                    type: 'normalInput',
                    value: '',
                    placeHolder: '11位手机号',
                    key: 'deepSearchParam.customerContact',
                    errorMsg: '格式：11位手机号',
                    limit: 'number',
                    length: 13
                  }
                },
                {
                  classes: 'halfWidth hasMarginLi',
                  type: 'input',
                  isEmpty: true,
                  label: '用户表号',
                  options: {
                    width: width,
                    type: 'normalInput',
                    placeHolder: '1-14位数字',
                    value: '',
                    key: 'deepSearchParam.deviceNo',
                    regular: 'deviceNo',
                    errorMsg: '格式：1-14数字',
                    limit: 'number',
                    length: 14
                  }
                },
                {
                  classes: 'halfWidth',
                  type: 'input',
                  isEmpty: true,
                  label: '表计户号',
                  options: {
                    width: width,
                    type: 'normalInput',
                    placeHolder: '1-12位数字',
                    value: '',
                    key: 'deepSearchParam.meterUserNo',
                    errorMsg: '格式：1-12数字',
                    limit: 'number',
                    length: 12
                  }
                },
                {
                  classes: 'halfWidth hasMarginLi',
                  type: 'select',
                  isEmpty: true,
                  label: '数据类型',
                  options: {
                    width: width,
                    cs: 'client',
                    data: this.getEnumItem('isRealTime').obj,
                    key: 'deepSearchParam.isRealTime'
                  }
                },
                {
                  classes: 'halfWidth',
                  type: 'select',
                  isEmpty: true,
                  label: '开户状态',
                  options: {
                    width: width,
                    cs: 'client',
                    data: this.getEnumItem('isAccount').obj,
                    key: 'deepSearchParam.status'
                  }
                },
                {
                  classes: 'halfWidth hasMarginLi',
                  type: 'input',
                  isEmpty: true,
                  label: '用户地址',
                  options: {
                    width: width,
                    type: 'normalInput',
                    value: '',
                    placeHolder: '50个字符内',
                    key: 'deepSearchParam.customerAddr',
                    length: 50,
                    regular: 'address'
                  }
                }
              ]
            }

          },
          deviceType: function () {
            return this.searchParam.deviceType
          },
          timeOpt: function () {
            let dates = this.initDate();
            return {
              key: 'measureDate',
              value: {startDate: dates.start, endDate: dates.end},
              width: '23em',
              fontSize: '0.8em'
            }
          },
          column() {
            let baseCols= [
              {
                title: '时间',
                field: 'collectTime',
                textAlign: 'center',
                width: 140,
                type: 'string',
                formatter: function (val, index, row) {
                  if (val !== '' && val !== 'null' && val !== null && typeof val !== 'undefined') {
                    return val.substr(0, 16)
                  }
                  return '--'
                }
              },
              {title: '用户姓名', field: 'customerName', textAlign: 'center', width: 130},
              {title: '门牌编号', field: 'propertyName', textAlign: 'center',  width: 180},
              {
                title: '数据类型',
                field: 'isRealTime',
                textAlign: 'center',
                width: 130,
                type: 'string',
                formatter: function (val, index, row, obj) {
                  return  '<span>' + obj.$parent.getEnumItem('isRealTime', val).text + '</span>'
                }
              },
              {
                title: '用户表号',
                field: 'deviceNo',
                textAlign: 'center',
                sortable: false,
                width: 200
              },
              {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: 120},
              {title: '用户地址', field: 'customerAddr', textAlign: 'center',  width: 260},
            ];
            let selCollItem=this.collectItem.join(',');
            let appIndex=baseCols.length-3;
            if (/0001ff00/gi.test(selCollItem)) {
              baseCols.splice(baseCols.length-3, 0,{title: this.searchParam.deviceType !== '07' ? '有功总示数' : '正向有功总', field: 'pr0',  textAlign: 'center',  width: 100})
            }
            if (/00900200/gi.test(selCollItem)) {
              baseCols.splice(baseCols.length-3, 0, {title: '账户余额（元）', field: 'balance', textAlign: 'center',  width: 120})
            }
            if (/00010001/gi.test(selCollItem)) {
              baseCols.splice(baseCols.length-3, 0, {title: '月冻结', field: 'monthFreezeP', textAlign: 'center', width: 120})
            }
            if (/05060101/gi.test(selCollItem)) {
              baseCols.splice(baseCols.length-3, 0, {title: '上1日冻结', field: 'dayFreezeP', textAlign: 'center', width: 120})
            }
             return baseCols;
          }
        },
        mounted: function () {
            let that = this;
            that.isIn();
            that.getAction();
          // window.onresize = function () {
          //   that.windowChange()
          // }

        },
        watch: {
            'searchParam.deviceType': {
                handler(val, oldVal) {
                    if (val !== oldVal) {
                      let dates = this.initDate();
                      this.$refs['inputs'][0].search = '';
                      this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startDate', 'endDate', 'isRealTime']);
                      if (dates.start !== this.searchParam['startDate'] || dates.end !== this.searchParam['endDate'] ) {
                        this.$refs.timer[0].dateOpt.value = {startDate: dates.start, endDate: dates.end};
                        this.$refs.timer[0].init();
                        this.resetDeepData(val);
                        this.measureDate = dates.start + '至' + dates.end;
                      } else {
                        this.getParams(1)
                      }
                    }
                }
            },
           'searchParam.isRealTime': {
              handler(val, oldVal) {
                this.getParams(1)
              }
            },
           'measureDate': {
                handler(val, oldVal) {
                    if (val !== '' && val !== oldVal) {
                        let arr = val.split('至');
                        this.searchParam.startDate = arr[0];
                        this.searchParam.endDate = arr[1];
                        this.getParams(1)
                    }
                }
            },
          $route(to, from) {
             //this.closeDiv();
           }
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
</style>



